import React from 'react';
import {observer} from "mobx-react";
import Search from 'Search';

const ViewDesktop = (props) => {


	return(		
		<div className="container flex minh100 no-padding">
			<Search />
		</div>
	)
}

export default observer(ViewDesktop);


